import Layout from "layout/Layout";
import React, { useEffect, useState } from "react";
import projectService from "services/project.service";
import { useParams, useSearchParams } from "react-router-dom";
import Gallery from "components/ProjectView/Components/ProjectUI/Bottom/Gallery";

const GalleryPage = (props) => {
  const [projectData, setProjectData] = useState(null);

  const { featured } = props;

  let { id } = useParams();

  const [searchParams] = useSearchParams();
  if (!id) {
    id = searchParams.get("project");
  }

  useEffect(() => {
    setProjectData(null);
    if (featured)
      projectService.getFeaturedProject().then((response) => {
        setProjectData({
          ...response.data.project,
          properties: response.data.properties,
        });
      });
    else
      projectService.getProjectByUrl(id).then((response) => {
        setProjectData({
          ...response.data.project,
          properties: response.data.properties,
        });
      });
  }, [id, featured]);
  return (
    <Layout loading={!projectData} isProject>
      <Gallery
        project={projectData}
        activeUI={[]}
        open
        uiData={projectData?.uiData || {}}
      />
    </Layout>
  );
};

export default GalleryPage;
